import React, { Component } from 'react';
import CabezaDos from './cabeza2';
import Navegacion from './navegacion';
import '../css/panel_video.css';

import keiko from '../../images/keiko.jpg';
import pedro from '../../images/pedro castillo.jpeg';

export default class PanelVideo extends Component {

    state = {
        date: new Date(),
        conexiones: 0
    };

    componentDidMount() { 
        //Fecha actual
        this.timer = setInterval(() => {
            this.setState({ date: new Date()});
        }, 1000);
    }

    componentWillUnmount (){
        clearInterval(this.timer)
    }

    months = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];
    days = ['Domingo', 'Lunes', 'Martes', 'Miércoles', 'Jueves', 'Viernes', 'Sábado'];

    hora = this.state.date.getHours() % 12? this.state.date.getHours() % 12:"12";
    notation = this.state.date.getHours() >= 12? "PM":"AM";

    render() {
        return (<div>
            <CabezaDos/>
            <Navegacion/>
            <div className="fondoSolar">
            <section className="container-fluid">
                <section className="row py-5">

                    <section className="col-lg-6 text-center pb-5">
                        <div className="fotoPanel">
                            <img className="cuadroPumiiUni" src={keiko} alt="Imagen PUMII UNI"/>
                            
                        </div>
                        <button type="submit" value="Submit" className="btn-block btn fondoprincipal text-white nexa">Votar Keiko</button>
                    </section>
            
                    <section className="col-lg-6 text-center">
                        
                        <div className="fotoPanel">
                            <img className="cuadroPumiiUni" src={pedro} alt="Imagen Logo UNI"/>
                            
                        </div>
                        <button type="submit" value="Submit" className="btn-block btn fondoprincipal text-white nexa ">Votar Pedro</button>
                    </section>

                </section>
            </section>
            </div></div>
        )
    }
}
