import React, { Component } from 'react';
import 'font-awesome/css/font-awesome.min.css';
import '../css/pagina_principal.css';
import CabezaPrincipal from './cabeza_pagprincipal';
import Navegacion from './navegacion';
import Footer from './footer';

import UNI from '../../images/UNI.png';
import keiko from '../../images/keiko.jpg';
import pedro from '../../images/pedro castillo.jpeg';

export default class ContenidoPrincipal extends Component {

  styleOne() {
    return {
      marginTop: '30px'
    }
  }

  stylePhotoPumii() {
    return {
      height: '200px',
      width: '200px'
    }
  }

  stylePhotoTeamPumii() {
    return {
      height: '20%',
      width: '100%'
    }
  }

  stylePhotoERC(){
    return {
      width: '50%'
    }
  }

  stylePhotoHistoria(){
    return{
      width: '100%'
    }
  }

  render() {
    return (
      <div className="m-0 p-0">
        <Navegacion/>
        <CabezaPrincipal/>
      <div className="container-fluid alineadoretos" style={this.styleOne()}>
        <div className="row">
            <div className="container-fluid col-lg-6 py-5 text-white texto-centrado">
              <p className="h5 font-futura nexa">Keiko Sofía Fujimori Higuchi nació el 25 de mayo de 1975 en el distrito de Jesús María de Lima, la capital del Perú.5​3​ Es la hija mayor de Alberto Fujimori y Susana Higuchi, ambos descendientes de inmigrantes japoneses.5​1​ Además de ella, sus padres tuvieron otros tres hijos: Hiro Alberto (nacido en diciembre de 1976), Sachi Marcela (nacida en marzo de 1979) y Kenji Gerardo (nacido en mayo de 1980).6​ Keiko y sus hermanos estudiaron en el colegio Sagrados Corazones Recoleta, una institución educativa privada administrada por la Iglesia Católica. En esta escuela, asistió a la educación primaria y secundaria.</p>
            </div><br/>
            <div className="container-fluid col-lg-6 py-5 imageERC text-center">
            <img src={keiko} style={this.stylePhotoHistoria()} alt="Foto Logo ERC" />
            </div><br/>
        </div>
      </div>

      <div className="container-fluid bg-white alineadoretos" style={this.styleOne()}>
        <div className="row">
            <div className="col-lg-6">
            <p className="h5 text-asme font-futura rellenotres">Pedro Castillo nació el 19 de octubre de 1969 en el poblado de Puña, distrito de Tacabamba, provincia de Chota en la región Cajamarca,5​ es el tercero de nueve hermanos, hijos de Ireño Castillo Núñez y Mavila Terrones Guevara. Cursó sus primeros estudios del 1° al 3° grado, en la Escuela Rural N° 10465 de Puña y concluyó sus estudios primarios en la escuela rural vecina n.° 10475 del caserío de Chugur en el distrito de Anguía, en la misma provincia de Chota.</p>
            </div><br/>
            <div className="container col-lg-6 py-5 text-center">
            <img src={pedro} style={this.stylePhotoHistoria()} alt="Foto entrada" />
            </div><br/>
        </div>
      </div>

    
      <div className="container-fluid bg-light py-2 m-0 alineadoretos">
      
      <div className="col-md-12 text-center">
      <div className="h1 font-weight-bolder nexa mb-0 py-3">PATROCINADORES</div>
      <div className="row justify-content-center px-2 py-3">
          <div className="col-md-4 py-3 "><img className="logouni" src={UNI} alt="Foto UNI" /></div>
          </div>
        </div>
      </div>
      
      <Footer/>
    </div>
    )
  }
}