import React, { Component } from 'react';
import '../css/login.css';
import 'font-awesome/css/font-awesome.min.css';

export default class Footer extends Component {

    styleFooter(){
        return {
            marginBottom: '0'
        }
    }

    render() {
        return (
            <div className="jumbotron text-center" style={this.styleFooter()}>
                <div className="row justify-content-center">
                    <div className="col-md-8">
                    <div className="h3 text-asme nexa">ENCUENTRANOS EN</div>

                        <div className="h3">
                            <a className="text-decoration-none px-2" href="https://instagram.com" target="_blank" rel="noopener noreferrer">
                                <i className="fa fa-instagram text-asme" aria-hidden="true"></i> </a>

                            <a className="text-decoration-none px-2" href="https://facebook.com" target="_blank" rel="noopener noreferrer">
                                <i className="fa fa-facebook text-asme" aria-hidden="true"></i> </a>

                            <a className="text-decoration-none px-2" href="https://linkedin.com" rel="noopener noreferrer" target="_blank">
                                <i className="fa fa-linkedin text-asme" aria-hidden="true"></i> </a>

                            <a className="text-decoration-none px-2" href="https://youtube.com" target="_blank" rel="noopener noreferrer">
                                <i className="fa fa-youtube text-asme" aria-hidden="true"></i> </a>

                        </div>

                        <div className="h3">
                            <a className="text-decoration-none px-2 text-asme nexa lead" href="https://github.com/GustavoDextre" target="_blank" rel="noopener noreferrer">@GADZPeru</a></div>

                        <div className="font-futura text-asme">
                         Ubicación: Av. Túpac Amaru 210 - Rímac. Apartado 1301
                        </div>

                        <div className="font-futura text-secondary mt-2">
                            &copy; GADZPeru - Todos los derechos reservados
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}